import { Component, H, connect } from "../Component";
import { GroupBrowserState } from "./_reducers/index";
export { GroupBrowserState, reducers, initialState } from "./_reducers/index";
export { deselectGroups, selectGroups, search } from "./_actions/index";
import { gotoPage } from "./_actions/index";

import Pager from "../Navigation/Pager/index";
import SearchArea from "./SearchArea";
import ResultsPanel from "./ResultsPanel";
import { GroupBrowserStore } from "../wrappers/GroupBrowser";

export interface GroupBrowserProps {
    /**
     * A unique key for the `GroupBrowser` component.
     * @type {string}
     */
    key: string;

    /**
     * The page currently being viewed in the `GroupBrowser`.
     * @type {number}
     */
    currentPage: number;

    /**
     * The number of groups to display per page in the `GroupBrowser`.
     * @type {number}
     */
    groupsPerPage: number;

    /**
     * The current number of groups displayed.
     * @type {number}
     */
    currentTotal: number;

    /**
     * Handler for when a page is selected.
     * @type {function}
     */
    gotoPage: (page: number) => void;
}

/**
 * A `GroupBrowser` component for browsing and selecting from an organization's groups.
 */
export class GroupBrowser extends Component<GroupBrowserProps> {
    constructor(props: GroupBrowserProps) {
        super(props);

        this.handlePageChange = this.handlePageChange.bind(this);
    }

    public render(tsx: H) {
        return (
            <div class="group-browser__container">
                <SearchArea key="group-browser-search-area" />
                <ResultsPanel key="group-browser-results-panel" />
                <Pager
                    key="group-browse-pager"
                    numPages={
                        this.props.groupsPerPage > this.props.currentTotal ?
                            1 :
                            Math.ceil(
                                this.props.currentTotal /
                                this.props.groupsPerPage
                            )
                    }
                    currentPage={this.props.currentPage}
                    onPageChange={this.handlePageChange}
                />
            </div>
        );
    }

    private handlePageChange(page: number) {
        this.props.gotoPage(page);
    }
}

interface StateProps {
    currentPage: number;
    groupsPerPage: number;
    currentTotal: number;
}

interface DispatchProps {
    gotoPage: (page: number) => void;
}

/**
 * `GroupBrowser` component connected to a `GroupBrowserStore`.
 */
export default connect<GroupBrowserProps, GroupBrowserStore, StateProps, DispatchProps>(
    (state) => ({
        currentPage: state.ui.page.number,
        groupsPerPage: state.config.groupsPerPage,
        currentTotal: state.results.currentQuery.total
    }),
    (dispatch) => ({
        gotoPage: (page: number) => dispatch(gotoPage(page))
    })
)(GroupBrowser);
