import { scrubGroupInfo } from "../../../_utils/index";
import { Pojo } from "../../../Component";

export const SELECT_GROUPS = "SELECT_GROUPS";
export const DESELECT_GROUPS = "DESELECT_GROUPS";

export const selectGroups = (groups: Pojo[]) => ({
    type: SELECT_GROUPS,
    payload: groups.map((group) => scrubGroupInfo(group))
});

export const deselectGroups = (groups: Pojo[]) => ({
    type: DESELECT_GROUPS,
    payload: groups
});