import { Component, Pojo, connect, H } from "../../Component";
import LoaderBars from "../../Loaders/LoaderBars";
import GroupsArea from "./GroupsArea";
import TotalArea from "./TotalArea";
import { selectGroups, deselectGroups } from "../_actions";
import { SortField } from "../../Dropdowns/SortDropdown";
import { GroupBrowserStore } from "../../wrappers/GroupBrowser";

export interface ResultsPanelProps {
    /**
     * A unique key for the `ResultsPanel` component.
     * @type {string}
     */
    key: string;

    /**
     * The current status of the results.
     * @type {string}
     */
    status: string;

    /**
     * The total number of results for the current query.
     * @type {number}
     */
    resultTotal: number;

    /**
     * The number of groups to display per page.
     * @type {number}
     */
    groupsPerPage: number;

    /**
     * The current page number in the browser.
     * @type {number}
     */
    pageNumber: number;

    /**
     * The groups to display on the current page.
     * @type {array}
     */
    displayGroups: Pojo[];

    /**
     * Mapping of selected groups.
     * @type {object}
     */
    selectedGroups: { [groupId: string]: Pojo };

    /**
     * The currently selected sort field.
     * @type {string}
     */
    sortField: SortField;

    /**
     * Handler for when a group or groups is/are selected.
     * @type {function}
     */
    selectGroups: (groups: Pojo[]) => void;

    /**
     * Handler for when a group or groups is/are deselected.
     * @type {function}
     */
    deselectGroups: (groups: Pojo[]) => void;
}

/**
 * A results panel showing a list of groups.
 */
export class ResultsPanel extends Component<ResultsPanelProps> {
    constructor(props: ResultsPanelProps) {
        super(props);

        this.handleGroupCheck = this.handleGroupCheck.bind(this);
        this.handleGroupUncheck = this.handleGroupUncheck.bind(this);
    }

    public render(tsx: H) {
        const loading = this.props.status === "loading";
        const sectionContainerClasses = {
            "gb-results-panel__section-container": true,
            "gb-results-panel__section-container--loading": loading
        };

        const totalItems = this.props.resultTotal;
        const startNum = totalItems === 0 ?
            0 : this.props.groupsPerPage * (this.props.pageNumber - 1) + 1;
        const endNum = totalItems < this.props.groupsPerPage * this.props.pageNumber ?
            totalItems : this.props.groupsPerPage * this.props.pageNumber;

        return (
            <div class="gb-results-panel__container">
                {
                    loading ?
                    <LoaderBars key="group-browser-loader-bars" /> : null
                }
                <section classes={sectionContainerClasses}>
                    <TotalArea
                        key="group-browser-totals"
                        startNum={startNum}
                        endNum={endNum}
                        totalItems={totalItems}
                    />
                </section>
                <section classes={sectionContainerClasses}>
                    <GroupsArea
                        key="groups-area"
                        displayGroups={this.props.displayGroups}
                        selectedGroups={this.props.selectedGroups}
                        sortField={this.props.sortField}
                        handleGroupCheck={this.handleGroupCheck}
                        handleGroupUncheck={this.handleGroupUncheck}
                    />
                </section>
            </div>
        );
    }

    private handleGroupCheck(group: Pojo) {
        this.props.selectGroups([group]);
    }

    private handleGroupUncheck(group: Pojo) {
        this.props.deselectGroups([group]);
    }
}

interface StateProps {
    status: string;
    resultTotal: number;
    groupsPerPage: number;
    pageNumber: number;
    displayGroups: Pojo[];
    selectedGroups: { [groupId: string]: Pojo };
    sortField: SortField;
}

interface DispatchProps {
    selectGroups: (groups: Pojo[]) => void;
    deselectGroups: (groups: Pojo[]) => void;
}

/**
 * `ResultsPanel` component connected to a `GroupBrowserStore`.
 */
export default connect<ResultsPanelProps, GroupBrowserStore, StateProps, DispatchProps>(
    (state) => ({
        status: state.results.groups.status,
        resultTotal: state.results.currentQuery.total,
        groupsPerPage: state.config.groupsPerPage,
        pageNumber: state.ui.page.number,
        displayGroups: state.results.groups.displayGroups,
        selectedGroups: state.results.groups.selectedGroups,
        sortField: state.parameters.sort.field
    }),
    (dispatch) => ({
        selectGroups: (groups) => dispatch(selectGroups(groups)),
        deselectGroups: (groups) => dispatch(deselectGroups(groups))
    })
)(ResultsPanel);
