import * as i18n from "dojo/i18n!../nls/resources";
import { Component, H, Pojo, connect, ComponentProps } from "../../Component";
import { selectGroups, deselectGroups } from "../_actions";
import Chip from "../../Badges/Chip";
import Ago2018Checkbox from "../../Buttons/Ago2018Checkbox";
import { GroupBrowserStore } from "../../wrappers/GroupBrowser";

export interface TotalAreaProps extends ComponentProps {
    /**
     * Number of the last group shown in the results list.
     * @type {number}
     */
    endNum: number;

    /**
     * Number of the first group shown in the results list.
     * @type {number}
     */
    startNum: number;

    /**
     * Total number of groups shown in the results list.
     * @type {number}
     */
    totalItems: number;

    /**
     * Array of groups displayed in the results.
     * @type {array}
     */
    displayGroups: Pojo[];

    /**
     * Mapping of currently selected groups.
     * @type {object}
     */
    selectedGroups: { [groupId: string]: Pojo };

    /**
     * Handler for selecting one or more groups.
     * @type {function}
     */
    selectGroups: (groups: Pojo[]) => void;

    /**
     * Handler for deselecting one or more groups.
     * @type {function}
     */
    deselectGroups: (groups: Pojo[]) => void;
}

export class TotalArea extends Component<TotalAreaProps> {
    constructor(props: TotalAreaProps) {
        super(props);

        this.handleCheckClick = this.handleCheckClick.bind(this);
        this.handleClearSelection = this.handleClearSelection.bind(this);
    }
    
    public render(tsx: H) {
        const { displayGroups, selectedGroups } = this.props;

        const anyResultsChecked = displayGroups.reduce(
            (result: boolean, current): boolean => {
                if (selectedGroups[current.id]) {
                    return true;
                }
                return result;
            },
            false
        );

        const allResultsChecked = displayGroups.reduce(
            (result: boolean, current): boolean => {
                if (!selectedGroups[current.id]) {
                    return false;
                }
                return result;
            },
            true
        );

        if (anyResultsChecked) {
            return (
                <div class="gb-total-area__container" key="gb-total-area-checked">
                    {
                        allResultsChecked ? (
                            <Ago2018Checkbox
                                key="gb-total-area__checked-input"
                                checked={true}
                                indeterminate={false}
                                onChange={this.handleClearSelection}
                            />
                        ) : (
                            <Ago2018Checkbox
                                key="gb-total-area__indeterminate-input"
                                checked={false}
                                indeterminate={true}
                                onChange={this.handleCheckClick}
                            />
                        )
                    }
                    <span class="gb-total-area__result">
                        <Chip
                            color="green"
                            text={`${displayGroups.filter((group) => selectedGroups[group.id]).length} selected`}
                        />
                        <button
                            class="gb-total-area__clear-btn"
                            onclick={this.handleClearSelection}
                        >
                            {i18n.clearSelection}
                        </button>
                    </span>
                </div>
            );
        }

        return (
            <div class="gb-total-area__container" key="gb-total-area">
                <Ago2018Checkbox
                    key="gb-total-area__select-all-input"
                    checked={false}
                    onChange={this.handleCheckClick}
                    title={i18n.selectAll}
                />
                <label class="gb-total-area__label">
                    {`${this.props.startNum}-${this.props.endNum} of ${this.props.totalItems}`}
                </label>
                <span class="gb-total-area__result">
                    {`${this.props.startNum}-${this.props.endNum} of ${this.props.totalItems}`}
                </span>
            </div>
        );
    }

    private handleCheckClick() {
        this.props.selectGroups(this.props.displayGroups);
    }

    private handleClearSelection() {
        this.props.deselectGroups(
            this.props.displayGroups.filter(
                (group) => this.props.selectedGroups[group.id]
            )
        );
    }
}

interface StateProps {
    displayGroups: Pojo[];
    selectedGroups: { [groupId: string]: Pojo };
}

interface DispatchProps {
    selectGroups: (groups: Pojo[]) => void;
    deselectGroups: (groups: Pojo[]) => void; 
}

export default connect<TotalAreaProps, GroupBrowserStore, StateProps, DispatchProps>(
    (state) => ({
        displayGroups: state.results.groups.displayGroups,
        selectedGroups: state.results.groups.selectedGroups
    }),
    (dispatch) => ({
        selectGroups: (groups: Pojo[]) => dispatch(selectGroups(groups)),
        deselectGroups: (groups: Pojo[]) => dispatch(deselectGroups(groups))
    })
)(TotalArea);
