import { combineReducers } from "../../../Component";
import currentQuery, { CurrentQueryState, initialState as initialCurrentQueryState } from "./currentQuery";
import groups, { GroupsState, initialState as initialGroupsState } from "./groups";

export interface ResultsState {
    currentQuery: CurrentQueryState;
    groups: GroupsState;
}

export const initialState = {
    currentQuery: initialCurrentQueryState,
    groups: initialGroupsState
};

export default combineReducers<ResultsState>({
    currentQuery,
    groups
});