import { GroupBrowserState } from "../../_reducers/index";
import { search } from "../results";
import { ThunkDispatch } from "../../../Component";

export const GOTO_PAGE = "GOTO_PAGE";

export const gotoPage = (page: number) =>
    (dispatch: ThunkDispatch<GroupBrowserState>, getState: () => GroupBrowserState) => {
        const state = getState();
        dispatch({ type: GOTO_PAGE, payload: page });
        dispatch(search({
            start: ((page - 1) * state.config.groupsPerPage) + 1
        }));
    };