export interface ConfigState {
    /**
     * Number of groups to show per page
     */
    groupsPerPage: number;
    /**
     * Base query on top of which sorting / filtering will be applied
     */
    baseQ: string;
}

export const initialState = {
    groupsPerPage: 10,
    baseQ: ""
};

export default (state: ConfigState = initialState) => state;