import { Pojo } from "../../Component";

export interface UtilsState {
    /**
     * 3x of 4x portal instance (must be loaded already)
     */
    portal: Pojo;
    /**
     * 3x or 4x request function
     */
    request: (url?: string, options?: Pojo) => dojo.Deferred<any>;
}

export const initialState: UtilsState = {
    portal: {},
    request: (url?: string) => null as any
};

export default (state: UtilsState = initialState) => state; 