import GroupCheckCard from "../../GroupCards/GroupCheckCard";
import { H, Pojo, ComponentProps } from "../../Component";

export interface GroupsAreaProps extends ComponentProps {
    /**
     * Array of groups to display in the results.
     * @type {array}
     */
    displayGroups: Pojo[];

    /**
     * Mapping of currently selected groups.
     * @type {object}
     */
    selectedGroups: {
        [groupId: string]: Pojo;
    };

    /**
     * The current sort field.
     * @type {string}
     */
    sortField: string;

    /**
     * Handler for when a group is selected.
     * @type {function}
     */
    handleGroupCheck: (group: Pojo) => void;

    /**
     * Handler for when a group is deselected.
     * @type {function}
     */
    handleGroupUncheck: (group: Pojo) => void;
}

/**
 * The groups area for the `GroupBrowser` component.
 */
export default (
    {
        displayGroups,
        handleGroupCheck,
        handleGroupUncheck,
        selectedGroups,
        sortField,
    }: GroupsAreaProps,
    tsx: H
) => (
    displayGroups.map(
        (group) => (
            <GroupCheckCard
                checked={!!selectedGroups[group.id]}
                group={group}
                key={group.id}
                sortField={sortField}
                onCheckGroup={handleGroupCheck}
                onUncheckGroup={handleGroupUncheck}
            />
        )
    )
);
