import { combineReducers } from "../../Component";

import utils, { UtilsState, initialState as initialUtilsState } from "./utils";
import parameters, { ParametersState, initialState as initialParametersState } from "./parameters";
import results, { ResultsState, initialState as initialResultsState } from "./results";
import ui, { UIState, initialState as initialUIState } from "./ui";
import config, { ConfigState, initialState as initialConfigState } from "./config";

export interface GroupBrowserState {
    config: ConfigState;
    parameters: ParametersState;
    results: ResultsState;
    ui: UIState;
    utils: UtilsState;
}

export const initialState = {
    config: initialConfigState,
    parameters: initialParametersState,
    results: initialResultsState,
    ui: initialUIState,
    utils: initialUtilsState
};

export const reducers = combineReducers<GroupBrowserState>({
    config,
    parameters,
    results,
    ui,
    utils
});
