import { GOTO_PAGE, LOAD_GROUPS_SUCCESS, LOAD_GROUPS_FAIL } from "../../_actions";
import { Action } from "../../../Component";

export interface PageState {
    number: number;
}

export const initialState: PageState = {
    number: 1
};

export default (state: PageState = initialState, action: Action) => {
    switch (action.type) {
        case GOTO_PAGE:
            return {
                ...state,
                number: action.payload
            };
        case LOAD_GROUPS_SUCCESS:
            return {
                ...state,
                number: action.payload.num > action.payload.total ?
                    1 :
                    Math.ceil(action.payload.start / action.payload.num)
            };
        case LOAD_GROUPS_FAIL:
            return {
                ...state,
                number: 1
            };
        default:
            return state;
    }
};
