import { TOGGLE_SORT_DROPDOWN, CHANGE_SORT_FIELD, CHANGE_SORT_ORDER } from "../../_actions";
import { Action } from "../../../Component";

export type SortState = boolean;
export const initialState: SortState = false;

export default (state: SortState = initialState, action: Action) => {
    switch (action.type) {
        case CHANGE_SORT_FIELD:
            return !state;
        case CHANGE_SORT_ORDER:
            return !state;
        case TOGGLE_SORT_DROPDOWN:
            return !state;
        default:
            return state;
    }
};
