import { LOAD_GROUPS_SUCCESS } from "../../_actions/index";
import { Action } from "../../../Component";

export interface CurrentQueryState {
    start: number;
    total: number;
}

export const initialState: CurrentQueryState = {
    start: 1,
    total: 0,
};

export default (state: CurrentQueryState = initialState, action: Action) => {
    switch (action.type) {
        case LOAD_GROUPS_SUCCESS:
            return {
                start: action.payload.start,
                total: action.payload.total
            };
        default:
            return state;
    }
};