import { GroupBrowserState } from "../../_reducers/index";
import { fetchGroups, mixinGroupInfo } from "../../../_utils/index";
import { Pojo } from "../../../Component";

export const LOADING_GROUPS = "LOADING_GROUPS";
export const LOAD_GROUPS_SUCCESS = "LOAD_GROUPS_SUCCESS";
export const LOAD_GROUPS_FAIL = "LOAD_GROUPS_FAILED";

export interface SearchOptions {
    start?: number;
}

export const search = (options?: SearchOptions) => (dispatch: any, getState: () => GroupBrowserState) => {
    dispatch({
        type: LOADING_GROUPS
    });
    dispatch(queryGroups(options)).then(
        (response: any) => {
            const state = getState();
            dispatch({
                type: LOAD_GROUPS_SUCCESS,
                payload: {
                    ...response,
                    results: response.results.map(
                        (group: Pojo) => mixinGroupInfo(state.utils.portal, group)
                    )
                }
            });
        },
        (response: any) => {
            dispatch({
                type: LOAD_GROUPS_FAIL,
                payload: response
            });
        }
    );
};

const queryGroups = (options?: SearchOptions) => (dispatch: any, getState: () => GroupBrowserState) => {
    const state = getState();
    const { request, portal } = state.utils;
    return fetchGroups(request, portal, {
        q: `${state.config.baseQ} ${state.parameters.searchString.current}`,
        num: state.config.groupsPerPage,
        sortField: state.parameters.sort.field,
        sortOrder: state.parameters.sort.order,
        start: options && options.start ? options.start : 1
    });
};
