import * as i18n from "dojo/i18n!./nls/resources";
import { Component, connect, H } from "../Component";
import { changeSearchString, changeSortOrder, changeSortField, search, toggleSortDropdown } from "./_actions";
import SortDropdown, { SortField, SortOrder } from "../Dropdowns/SortDropdown";
import { GroupBrowserStore } from "../wrappers/GroupBrowser";

export interface SearchAreaProps {
    /**
     * A unique key for the `SearchArea` component.
     * @type {string}
     */
    key: string;

    /**
     * The current search string to display in the input.
     * @type {string}
     */
    currentSearchString: string;

    /**
     * Controls whether or not the sort dropdown is open.
     * @type {boolean}
     */
    sortActive: boolean;

    /**
     * The currently selected sort field.
     * @type {string}
     */
    sortField: SortField;

    /**
     * The currently selected sort order.
     * @type {string}
     */
    sortOrder: SortOrder;

    /**
     * Handler for when the search string is changed.
     * @type {function}
     */
    changeSearchString: (str: string) => void;

    /**
     * Handler for when the sort field is changed.
     * @type {function}
     */
    changeSortField: (field: SortField) => void;

    /**
     * Handler for when the sort order is changed.
     * @type {function}
     */
    changeSortOrder: (order: SortOrder) => void;

    /**
     * Handler for when the user initiates a search.
     * @type {function}
     */
    search: () => void;

    /**
     * Handler for when the sort dropdown is toggled.
     * @type {function}
     */
    toggleSortDropdown: () => void;
}

/**
 * The search area for the `GroupBrowser`.
 */
export class SearchArea extends Component<SearchAreaProps> {
    constructor(props: SearchAreaProps) {
        super(props);

        this.handleInputChange = this.handleInputChange.bind(this);
        this.handleInputKeyDown = this.handleInputKeyDown.bind(this);
        this.handleSortFieldChange = this.handleSortFieldChange.bind(this);
        this.handleSortOrderChange = this.handleSortOrderChange.bind(this);
        this.handleSortClick = this.handleSortClick.bind(this);
    }

    public render(tsx: H) {
        return (
            <div class="gb-search-area__container">
                <svg width="20" height="20" viewBox="0 0 20 20">
                    <path
                        d="M19.205 18.295l-7.036-7.035A6.874 6.874 0 0 0 6.875 0 6.874 6.874 0 0 0 0 6.875a6.874 6.874 0 0 0 11.286 5.27l7.035 7.034.884-.884zM1.25 6.875A5.632 5.632 0 0 1 6.875 1.25 5.632 5.632 0 0 1 12.5 6.875 5.632 5.632 0 0 1 6.875 12.5 5.632 5.632 0 0 1 1.25 6.875z"
                        fill="#595959"
                        fill-rule="nonzero"
                    />
                </svg>
                <input
                    class="gb-search-area__input"
                    title={i18n.searchGroups}
                    aria-label={i18n.searchGroups}
                    type="search"
                    oninput={this.handleInputChange}
                    onkeydown={this.handleInputKeyDown}
                    value={this.props.currentSearchString}
                    placeholder={i18n.searchGroups}
                />
                <div class="gb-search-area__btn-section">
                    <SortDropdown
                        active={this.props.sortActive}
                        key="gb-sort-dropdown"
                        field={this.props.sortField}
                        order={this.props.sortOrder}
                        availableFields={["title", "owner", "created", "modified"]}
                        onFieldChange={this.handleSortFieldChange}
                        onOrderChange={this.handleSortOrderChange}
                        onClick={this.handleSortClick}
                    />
                </div>
            </div>
        );
    }

    private handleInputChange(e: any) {
        this.props.changeSearchString(e.target.value);
        if (e.target.value === "") {
            this.props.search();
        }
    }

    private handleInputKeyDown(e: KeyboardEvent) {
        if (e.keyCode === 13) {
            this.props.search();
        }
    }

    private handleSortFieldChange(field: SortField) {
        this.props.changeSortField(field);
        this.props.search();
    }

    private handleSortOrderChange(order: SortOrder) {
        this.props.changeSortOrder(order);
        this.props.search();
    }

    private handleSortClick() {
        this.props.toggleSortDropdown();
    }
}

interface StateProps {
    currentSearchString: string;
    sortActive: boolean;
    sortField: SortField;
    sortOrder: SortOrder;
}

interface DispatchProps {
    changeSearchString: (str: string) => void;
    changeSortField: (field: SortField) => void;
    changeSortOrder: (order: SortOrder) => void;
    search: () => void;
    toggleSortDropdown: () => void;
}

/**
 * `SearchArea` component connected to the `GroupBrowserStore`.
 */
export default connect<SearchAreaProps, GroupBrowserStore, StateProps, DispatchProps>(
    (state) => ({
        currentSearchString: state.parameters.searchString.current,
        sortActive: state.ui.sort,
        sortField: state.parameters.sort.field,
        sortOrder: state.parameters.sort.order
    }),
    (dispatch) => ({
        changeSearchString: (str: string) => dispatch(changeSearchString(str)),
        changeSortField: (field: SortField) => dispatch(changeSortField(field)),
        changeSortOrder: (order: SortOrder) => dispatch(changeSortOrder(order)),
        search: () => dispatch(search()),
        toggleSortDropdown: () => dispatch(toggleSortDropdown())
    })
)(SearchArea);
