import { CHANGE_SORT_FIELD, CHANGE_SORT_ORDER } from "../../_actions/index";
import { Action } from "../../../Component";

export interface SortState {
    field: "title" | "owner" | "created" | "modified";
    order: "asc" | "desc";
}

export const initialState: SortState = {
    field: "title",
    order: "asc"
};

export const defaultSortDirection = {
    relevance: "desc",
    title: "asc",
    created: "desc",
    owner: "asc",
    modified: "desc",
    numviews: "desc",
    avgrating: "desc"
};

export default (state: SortState = initialState, action: Action) => {
    switch (action.type) {
        case CHANGE_SORT_FIELD:
            return {
                ...state,
                field: action.payload,
                order: defaultSortDirection[action.payload]
            };
        case CHANGE_SORT_ORDER:
            return {
                ...state,
                order: action.payload
            };
        default:
            return state;
    }
};
