import {
    DESELECT_GROUPS,
    LOADING_GROUPS,
    LOAD_GROUPS_FAIL,
    LOAD_GROUPS_SUCCESS,
    SELECT_GROUPS
} from "../../_actions/index";
import { Pojo, Action } from "../../../Component";

export interface GroupsState {
    displayGroups: Pojo[];
    selectedGroups: {
        [groupId: string]: Pojo;
    };
    status: string;
}

export const initialState: GroupsState = {
    displayGroups: [],
    selectedGroups: {},
    status: "loading"
};

export default (state: GroupsState = initialState, action: Action) => {
    switch (action.type) {
        case DESELECT_GROUPS:
            return {
                ...state,
                selectedGroups: action.payload.reduce(
                    (result: GroupsState["selectedGroups"], current: Pojo): GroupsState["selectedGroups"] => {
                        if (result[current.id]) {
                            delete result[current.id];
                        }
                        return result;
                    },
                    state.selectedGroups
                )
            };
        case SELECT_GROUPS:
            return {
                ...state,
                selectedGroups: action.payload.reduce(
                    (result: GroupsState["selectedGroups"], current: Pojo): GroupsState["selectedGroups"] => {
                        result[current.id] = current;
                        return result;
                    },
                    state.selectedGroups
                )
            };
        case LOADING_GROUPS:
            return {
                ...state,
                status: "loading"
            };
        case LOAD_GROUPS_SUCCESS:
            return {
                ...state,
                displayGroups: action.payload.results,
                status: "loaded"
            };
        case LOAD_GROUPS_FAIL:
            return {
                ...state,
                displayGroups: [],
                status: "failed"
            };
        default:
            return state;
    } 
};
