import { combineReducers } from "../../../Component";
import page, { PageState, initialState as initialPageState } from "./page";
import sort, { SortState, initialState as initialSortState } from "./sort";

export interface UIState {
    page: PageState;
    sort: SortState;
}

export const initialState = {
    page: initialPageState,
    sort: initialSortState
};

export default combineReducers<UIState>({
    page,
    sort
});