import { CHANGE_SEARCH_STRING } from "../../_actions/index";
import { Action } from "../../../Component";

export interface SearchStringState {
    current: string;
    previous: string;
}

export const initialState: SearchStringState = {
    current: "",
    previous: ""
};

export default (state: SearchStringState = initialState, action: Action) => {
    switch (action.type) {
        case CHANGE_SEARCH_STRING:
            return {
                ...state,
                current: action.payload,
                previous: state.current
            };
        default:
            return state;
    }
};
