import { combineReducers } from "../../../Component";
import searchString, { SearchStringState, initialState as initialSearchStringState } from "./searchString";
import sort, { SortState, initialState as initialSortState } from "./sort";

export interface ParametersState {
    searchString: SearchStringState;
    sort: SortState;
}

export const initialState = {
    searchString: initialSearchStringState,
    sort: initialSortState
};

export default combineReducers<ParametersState>({
    searchString,
    sort
});
